<template>
  <f7-page>
    <f7-navbar title="Scan" back-link="Back" sliding></f7-navbar>
    <f7-block inner>Scan QRCode to update promotion screen.</f7-block>
    <f7-list form>
      <qrcode-reader class="qrcode-reader" @decode="onDecode"></qrcode-reader>
    </f7-list>
    <f7-block>{{msg}}</f7-block>
  </f7-page>
</template>

<script>
export default {
  data () {
    return {
      prices: null,
      msg: ''
    }
  },
  created () {
    let scanner = new instaScan.Scanner({ video: document.getElementById('preview') })
    scanner.addListener('scan', (content) => {
      console.log(content);
    })
    instaScan.Camera.getCameras().then( (cameras) => {
      if (cameras.length > 0) {
        scanner.start(cameras[0]);
      } else {
        console.error('No cameras found.');
      }
    }).catch( (e) => {
      console.error(e);
    })
  },
  methods: {
    onDecode (content) {
      this.msg = `Opened URL: \n${content}.`
      window.open(content, '_blank');
    }
  }
}
</script>
<style scoped>
.list-block {
  display: flex;
  justify-content: center;
  align-items: center;
}
.qrcode-reader {
  width: 86vw;
}
</style>


